/******* Section *******/
.section {
  position: relative;
  overflow: hidden;
  margin-top: 40px;
  padding: 40px 0;
}
.section:before {
  content: "";
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  opacity: 0.15;
}
.section.default:before {
  background-repeat: repeat;
  background-size: 350px;
  background-position: center;
  opacity: 1;
}
.section .section-title {
  text-align: center;
  margin-bottom: 12px;
  position: relative;
  font-size: 30px;
  font-family: "Truculenta", serif;
}
.section .section-title:after {
  content: "";
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  max-width: 160px;
  margin: 0 auto;
  height: 3px;
}
.section .section-desc {
  margin: 0 auto;
  text-align: center;
  max-width: 50%;
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 32px;
}

/******* Landing Page *******/
.landing-page .logo svg {
  fill: #fff;
}

/******* Frame box *******/
.frame-box {
  width: 100%;
  padding: 48px 0;
  box-sizing: border-box;
}
.frame-box .frame-image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  min-height: 500px;
  height: 100%;
  position: relative;
}
.frame-box .frame-image .top-mask:before {
  content: "";
  height: 50px;
  width: 40%;
  position: absolute;
  left: 0;
}
.frame-box .frame-image .top-mask:after {
  content: "";
  height: 35px;
  width: 57.3%;
  width: calc(60% - 30px);
  position: absolute;
  right: 15px;
  margin-top: 15px;
}
.frame-box .frame-image .bottom-mask:before {
  content: "";
  height: 50px;
  width: 40%;
  position: absolute;
  bottom: 0;
  left: 0;
}
.frame-box .frame-image .bottom-mask:after {
  content: "";
  height: 35px;
  width: 57.3%;
  width: calc(60% - 30px);
  position: absolute;
  right: 15px;
  bottom: 0;
  margin-bottom: 15px;
}
.frame-box .frame-image .content-wrapper {
  width: 100%;
  display: inline-block;
  height: 100%;
}
.frame-box .frame-image .content-wrapper .content {
  float: right;
  width: 600px;
  height: 100%;
  min-height: 410px;
  padding: 30px;
  vertical-align: middle;
  margin: 15px 15px;
  position: relative;
  box-sizing: border-box;
}
.frame-box:nth-child(even) {
  padding: 48px 0;
}
.frame-box:nth-child(even) .frame-image .top-mask:before {
  right: 0;
  left: auto;
}
.frame-box:nth-child(even) .frame-image .top-mask:after {
  left: 15px;
}
.frame-box:nth-child(even) .frame-image .bottom-mask:before {
  right: 0;
  left: auto;
}
.frame-box:nth-child(even) .frame-image .bottom-mask:after {
  left: 15px;
}
.frame-box:nth-child(even) .frame-image .content-wrapper .content {
  float: left;
}
.frame-box:last-child {
  padding-bottom: 0;
}

/******* Cart Items bottomSheet *******/
.mat-mdc-list.cart-items.mat-mdc-list-base {
  padding: 0;
}
.mat-mdc-list.cart-items.mat-mdc-list-base .mat-mdc-list-item {
