}

/******* Responsive *******/
@media screen and (max-width: 599px) {
  .mat-mdc-list.cart-items.mat-mdc-list-base .mat-mdc-list-item .mdc-list-item__primary-text a.menu-item-name {
    padding: 0;
  }
}
@media screen and (max-width: 959px) {
  .all-menu-items .search-sidenav {
    float: none;
    position: absolute;
  }
  .all-menu-items .search-sidenav .mat-drawer-inner-container {
    padding: 0px;
  }
  .all-menu-items .search-sidenav .mat-drawer-inner-container .mat-mdc-card {
    box-shadow: none;
    border-radius: 0;
  }
  .account .mat-drawer .mat-drawer-inner-container, .checkout .mat-drawer .mat-drawer-inner-container {
    padding: 0px;
  }
  .account .mat-drawer .mat-drawer-inner-container .mat-mdc-card, .checkout .mat-drawer .mat-drawer-inner-container .mat-mdc-card {
    box-shadow: none;
    border-radius: 0;
  }
  .section .section-title {
    font-size: 26px;
  }
  .section .section-desc {
    max-width: 90%;
    font-size: 14px;
  }
  .frame-box .frame-image {
    background-size: contain;
    background-position: top center;
  }
  .frame-box .frame-image .top-mask:before,
  .frame-box .frame-image .top-mask:after,
  .frame-box .frame-image .bottom-mask:before,
  .frame-box .frame-image .bottom-mask:after {
    display: none;
  }
  .frame-box .frame-image .content-wrapper .content {
    float: none;
    width: 90%;
    min-height: 350px;
    margin: 15px auto;
    margin-top: 45%;
  }
  .frame-box:nth-child(even) .frame-image .content-wrapper .content {
    float: none;
  }
  .reservation-form {
    padding: 0 0 24px 0;
    border: none !important;
    margin: 0 -16px;
  }
}
@media screen and (min-width: 960px) and (max-width: 1279px) {
  .frame-box .frame-image .top-mask:before {
    width: 30%;
  }
  .frame-box .frame-image .top-mask:after {
    width: calc(70% - 30px);
  }
  .frame-box .frame-image .bottom-mask:before {
    width: 30%;
  }
  .frame-box .frame-image .bottom-mask:after {
    width: calc(70% - 30px);
  }
  .frame-box .frame-image .content-wrapper .content {
    float: right;
    width: 550px;
    min-height: 410px;
  }
}
